import sample from "lodash-es/sample";

class Raffler {
	constructor(itemArr) {
		this.model = itemArr.map(item => ({
			item,
			isAvailable: true
		}));
	}

	// model

	_markAsAvailable(model, index) {
		return this._markAs(model, true, index);
	}

	_markAsDrawn(model, index) {
		return this._markAs(model, false, index);
	}

	_markAs(model, isAvailable, index) {
		model[index].isAvailable = isAvailable;
		return model;
	}

	// getters

	_getAvailableIndexesByItem(model, item) {
		return this._getIndexesByAvailabilityAndItem(model, true, item);
	}

	_getUnavailableIndexesByItem(model, item) {
		return this._getIndexesByAvailabilityAndItem(model, false, item);
	}

	_getAvailableIndexes(model) {
		return this._getIndexesByAvailabilityAndItem(model, true);
	}

	_getUnavailableIndexes(model) {
		return this._getIndexesByAvailabilityAndItem(model, false);
	}

	_getItem(model, index) {
		return model[index].item;
	}

	_getItemsByAvailability(model, isAvailable) {
		return model.filter(entry => entry.isAvailable === isAvailable).map(entry => entry.item);
	}

	_getIndexesByAvailabilityAndItem(model, isAvailable, item) {
		return model.map((entry, index) => index).filter(index => model[index].isAvailable === isAvailable && (item == null || model[index].item === item));
	}

	// controller

	/**
	 * Sorteia um item e o retorna, removendo-o dos próximos sorteios.
	 *
	 * @instance
	 * @memberOf Raffler
	 * @return {*}
	 * @public
	 *
	 * @example
	 * var bingo = new Raffler([0, 2, 4, 6, 8]);
	 * var itemSorteado = bingo.draw(); // 4
	 * var outroItemSorteado = bingo.draw(); // 6
	 * var itensRestantes = bingo.getAvailableItems(); // [0, 2, 8];
	 */
	draw() {
		let availableIndexes = this._getAvailableIndexes(this.model);

		if (availableIndexes.length > 0) {
			let randomIndex = sample(availableIndexes),
				randomItem = this._getItem(this.model, randomIndex);

			this.model = this._markAsDrawn(this.model, randomIndex);

			return randomItem;
		}
	}

	/**
	 * Devolve um item ao sorteio.
	 * 
	 * @instance
	 * @memberOf Raffler
	 * @param {*} item Item devolvido
	 * @public
	 *
	 * @example
	 * var bingo = new Raffler([0, 2, 4, 6, 8]);
	 * var itemSorteado = bingo.draw(); // 4
	 * bingo.putBack(itemSorteado);
	 * var itensRestantes = bingo.getAvailableItems(); // [0, 2, 4, 6, 8];
	 */
	putBack(item) {
		var returningIndex = this._getUnavailableIndexesByItem(this.model, item)[0];
		this.model = this._markAsAvailable(this.model, returningIndex);
	}

	/**
	 * Retorna os itens disponíveis para sorteio.
	 * 
	 * @instance
	 * @memberOf Raffler
	 * @return {Array}
	 * @public
	 *
	 * @example
	 * var bingo = new Raffler([0, 2, 4, 6, 8]);
	 * var itemSorteado = bingo.draw(); // 8
	 * var outroItemSorteado = bingo.draw(); // 2
	 * var itensDisponiveis = bingo.getAvailableItems(); // [0, 4, 6];
	 */
	getAvailableItems() {
		return this._getItemsByAvailability(this.model, true);
	}

	/**
	 * Retorna os itens indisponíveis para sorteio.
	 * 
	 * @instance
	 * @memberOf Raffler
	 * @return {Array}
	 * @public
	 *
	 * @example
	 * var bingo = new Raffler([0, 2, 4, 6, 8]);
	 * var itemSorteado = bingo.draw(); // 8
	 * var outroItemSorteado = bingo.draw(); // 2
	 * var itensNaoDisponiveis = bingo.getUnavailableItems(); // [2, 8];
	 */
	getUnavailableItems() {
		return this._getItemsByAvailability(this.model, false);
	}
}

export default Raffler;
